html, body {
  -webkit-font-smoothing: antialiased;
  font-family: "Conso", sans-serif;
  font-size: 100%;
}

*,
*:before,
*:after {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style: none;
}

p,
li,
span,
button,
input,
textarea,
table,
td,
th,
a,
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Conso", sans-serif;
}

.container {
  max-width: 1200px;
  width: 100%;
  margin: 0 auto;
}
@media (max-width: 1200px) {
  .container {
    width: 90%;
  }
}

.hide {
  display: none;
}

.show {
  display: inline-block;
}

header {
  padding: 0 30px;
  width: 100%;
}
@media (max-width: 1200px) {
  header {
    border-bottom: solid 1px var(--primary);
    padding: 0;
    position: fixed;
  }
}
header nav {
  align-items: center;
  display: flex;
  height: 100px;
  justify-content: space-between;
  position: relative;
  width: 100%;
}
@media (max-width: 1200px) {
  header nav {
    height: 80px;
    align-items: flex-end;
    justify-content: initial;
    padding-bottom: 10px;
  }
}
header nav .logo img {
  display: block;
  height: 100%;
  object-fit: contain;
  object-position: center;
  width: 100%;
}
@media (max-width: 1200px) {
  header nav .logo img {
    height: 60px;
    width: auto;
  }
}
header nav ul {
  align-items: center;
  list-style: none;
  display: flex;
  justify-content: space-between;
  width: 60%;
}
@media (max-width: 1200px) {
  header nav ul {
    flex-direction: column;
    align-items: flex-end;
    position: absolute;
    top: 90px;
    right: -3000px;
    width: 100%;
    transition: all 0.3s ease-in-out;
  }
}
header nav ul.active {
  right: 0;
}
header nav ul li {
  height: 35px;
}
@media (max-width: 1200px) {
  header nav ul li {
    height: 50px;
    width: 100%;
  }
}
header nav ul li:last-child {
  display: none;
}
@media (max-width: 1200px) {
  header nav ul li:last-child {
    display: block;
  }
  header nav ul li:last-child a i {
    font-size: 24px;
  }
}
header nav ul li a {
  color: var(--primary);
  display: inline-block;
  font-size: 16px;
  font-weight: 400;
  line-height: 1;
  padding: 6px 20px;
  text-decoration: none;
  transition: all 0.3s ease-in;
}
header nav ul li a:hover:not(.active), header nav ul li a.active {
  background-color: var(--primary);
  color: #fff;
}
@media (max-width: 1200px) {
  header nav ul li a {
    padding: 0 15px;
    line-height: 50px;
  }
}
header nav .userActions {
  display: flex;
  align-items: center;
}
@media (max-width: 1200px) {
  header nav .userActions {
    margin-left: auto;
  }
}
header nav .userActions a {
  display: block;
}
header nav .userActions a i {
  color: var(--primary);
  font-size: 24px;
}
header nav .userActions a:first-child {
  margin-right: 15px;
}
@media (max-width: 1200px) {
  header nav .userActions a:first-child {
    display: none;
  }
}
header nav .userActions a:hover i {
  color: var(--secondary);
}
header nav .mobMenu {
  display: block;
  margin-left: 20px;
}
@media (min-width: 1200px) {
  header nav .mobMenu {
    display: none;
  }
}
header nav .mobMenu:hover i {
  color: var(--secondary);
}
header nav .mobMenu i {
  font-size: 24px;
  color: var(--primary);
}
header nav .mobMenu i:not(.show) {
  display: none;
}